.App {
  margin: 0 auto;
  color: #f5eded;
  max-width: 1200px;
  text-align: center;
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.card {
  width: 300px;
  height: 300px;
  padding: 1em;
  margin: 1em;
  background-color: #3e3636;
  border-radius: 12px;
}

.card p {
  font-weight: bold;
}

.en-stock {
  color: greenyellow;
}

.timer {
  color: greenyellow;
}

.image {
  border-radius: 5px;
  width: auto;
  object-fit: cover;
}

.en-rupture {
  color: #d72323;
}

.title {
  text-align: left;
  color: #f5eded;
  padding: 1em;
}

.title-timer {
  text-align: left;
  padding-left: 1.5em;
}

.gclink {
  text-decoration: none;
  color: #f5eded;
}
